<style lang="less">
  @import '../../assets/index.less';
  @import './home.less';
  .contact-us-section {
		height: 284px;
		background: url(../../assets/img/home/contactusbar.png) no-repeat center;
		background-size: cover;
		color:#fff;
		span {
			cursor: pointer;
			background-color: #22c6eb;
			line-height: 60px;
			border-radius: 30px;
			text-align: center;
			color:#fff;
			padding:0 20px;
			&:hover {
				opacity: .7;
			}
		}
	}
	.inner {
		width: 1280px;
    height: 100%;
    margin: 0 auto;
	}
</style>
<template>
  <div class="home-wrapper">
    <!-- 轮播 -->
    <div class="home_slider">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="(item,index) in bannerData" :key="index">
          <a  target="_blank" :href="item.url">
            <img :src="'http://www.bitpui.com'+item.path" alt=""/>
          </a>
        </div>
      </div>
    </div>
    <!-- 轮播下公告 -->
    <div class="home_notice">
      <div class="home_notice_content">
        <div @click="$router.push({path:'/noticeData', query:{id:item.id}})" v-for="(item,index) in noticeData"
          class="item">
          {{ item.title }}
        </div>
      </div>
    </div>
    <!-- 主要内容区域 -->
    <div class="home_content">
      <!-- 主流币种 -->
      <div class="home_content_mainCoin">
        <ul>
          <li v-for="(item,index) in hotData" :key="index">
            <div class="coinBox">
              <div class="coinBox_name">
                <div class="name">
                  <img :src="$public.transCodeImg(item.code)" alt="" />
                  {{ item.name }}/USDT
                </div>
                <div class="changeRate" :id="item.code+'_changeRate'">
                  <span :class="item.changeRate.indexOf('-')> -1?'red':'green' ">
                    {{ item.changeRate }}
                  </span>
                </div>
              </div>
              <div class="coinBox_price">
                <span :class="`price ${item.changeRate.indexOf('-')> -1?'red':'green' } ${item.code+'_price'}`">
                  {{ $public.transPrice(item.price, item.code) }}
                </span>
                <span class="cnyPrice" :id="item.code+'_cnyPrice'">
                  ≈
                  <span :class="item.code+'_cnyPrice'">
                    {{ $public.toIntercept(item.cnyPrice, 2) }}
                  </span>
                  CNY
                </span>
              </div>
              <div class="coinBox_amount">
                {{ $t('home.quotations[0]') }}
                <!--
                                成交量
                            -->
                ：{{ $public.toIntercept(item.volume, 2) }}
              </div>
              <div v-if="theme =='day'" class="bg" :id="item.code+'_bg'" :class="item.changeRate.indexOf('-') > -1? 'bg-red':'bg-green'" ></div>
              <div v-else class="bg" :id="item.code+'_bg'" :class="item.changeRate.indexOf('-') > -1? 'bg-red-n':'bg-green-n'" ></div>
            </div>
          </li>
        </ul>
      </div>
      <!-- 行情列表 -->
      <div class="home_content_quotations">
        <!-- 头部 -->
        <div class="quotations_head">
          <div class="coinType cell">
            <!-- 币种 -->
            {{ $t('home.quotations[1]') }}
          </div>
          <div class="newPrice cell">
            <!-- 最新价 -->
            {{ $t('home.quotations[2]') }}
          </div>
          <div class="changeRate cell">
            <!-- 24H涨跌幅 -->
            {{ $t('home.quotations[3]') }}
          </div>
          <div class="price-high cell">
            <!-- 24H最高价 -->
            {{ $t('home.quotations[4]') }}
          </div>
          <div class="price-low cell">
            <!-- 24H最低价 -->
            {{ $t('home.quotations[5]') }}
          </div>
          <div class="amount cell">
            <!-- 24H成交量 -->
            {{ $t('home.quotations[6]') }}
          </div>
          <div class="openTime cell">
            <!-- 开盘时间 -->
            {{ $t('home.quotations[7]') }}
          </div>
        </div>
        <!-- 列表 -->
        <ul class="quotations_lsit">
          <li v-for="(item,index) in data">
            <div class="coinType cell">
              <img :src="$public.transCodeImg(item.code)" alt="" />
              {{item.name}}/USDT
            </div>
            <div class="newPrice cell">
              <span class="price" :class="item.code+'_price'">
                {{ $public.transPrice(item.price, item.code) }}
              </span>
              /
              <span :class="item.code+'_cnyPrice'" class="cnyPrice">
                {{ $public.toIntercept(item.cnyPrice, 2) }}
              </span>
            </div>
            <div class="changeRate cell" :class="item.code+'_changeRate'">
              <span :class="item.changeRate.indexOf('-')> -1 ?'red':'green'">
                {{ item.changeRate }}
              </span>
            </div>
            <div class="price-high cell" :class="item.code+'_high'">
              {{ item.high }}
            </div>
            <div class="price-low cell" :class="item.code+'_low'">
              {{ item.low }}
            </div>
            <div class="amount cell" :class="item.code+'_volume'">
              {{ $public.toIntercept(item.volume, 2) }}
            </div>
            <div class="openTime cell" :class="item.code+'date'">
              {{ item.date }}
            </div>
          </li>
        </ul>
      </div>
    </div>    
    <!-- 宣传 -->
    <div class="home_conduct">
      <div class="home_conduct_content">
        <div class="item" v-for="i in conduct">
          <img :src="i.img" alt="LOGO" />
          <h4>
            {{ i.title }}
          </h4>
          <p>
            {{ i.content }}
          </p>
        </div>
      </div>
    </div>
    <div class="contact-us-section">
				<div class="inner flex v hc">
					<p class="f46 wcr" style="font-size:36px;">{{$t('contct[0]')}}</p>
					<div class="flex sb vc f24" style="font-size:24px">
						<p >{{$t('contct[1]')}}</p>
						<span>{{$t('contct[2]')}}</span>
					</div>
				</div>
			</div>
  </div>
</template>
<script>
  import { indexGetpro, mainPushUrl } from "@/api/contract"
  import { bannerfind, zixun, appLink} from "@/api/public"
  import Swiper from 'swiper';
  export default {
    data() {
      return {
        appLink,
        theme:"",
        // ajax币种列表
        data: [],
        // 公告
        noticeData: "",
        // 轮播
        bannerData: "",
        swiper: {},
        mainPusher: "",
        conduct: [
          { img: require("../../assets/img/home/bc1.png"), title: this.$t('home.conduct[0]'), content: this.$t('home.conduct[1]') },
          { img: require("../../assets/img/home/bc2.png"), title: this.$t('home.conduct[2]'), content: this.$t('home.conduct[3]') },
          { img: require("../../assets/img/home/bc3.png"), title: this.$t('home.conduct[4]'), content: this.$t('home.conduct[5]') },
          { img: require("../../assets/img/home/bc4.png"), title: this.$t('home.conduct[6]'), content: this.$t('home.conduct[7]') },
        ]
      };
    },
    components: {},
    computed: {
      // 热门币种
      hotData() {
        var arr = ['BTC', 'ETH', 'ETC', 'LTC', 'EOS']
        var _arr = this.data.filter((item) => {
          return arr.includes(item.name)
        })
        return _arr
      }
    },
    methods: {
      // 推送
      mainPush() {
        if ("WebSocket" in window) {
          this.mainPusher = new WebSocket(mainPushUrl);
        } else if ("MozWebSocket" in window) {
          this.mainPusher = new WebSocket(mainPushUrl);
        } else {
          this.mainPusher = new WebSocket(mainPushUrl);
        }
        this.mainPusher.onopen = () => {
          var msg = { code: 'all' }
          this.mainPusher.send(JSON.stringify(msg))
        }
        this.mainPusher.onmessage = (msg) => {
          if (this.$route.path !== '/home') return
          var data = JSON.parse(msg.data)                     
          // 获取对应当前code的节点
          var cnyPrice = document.querySelectorAll(`.${data.code}_cnyPrice`)
          var price = document.querySelectorAll(`.${data.code}_price`)
          var changeRate = document.querySelectorAll(`.${data.code}_changeRate`)
          var volume = document.querySelectorAll(`.${data.code}_volume`)
          var high = document.querySelectorAll(`.${data.code}_high`)
          var low = document.querySelectorAll(`.${data.code}_low`)
          var bg = document.getElementById(`${data.code}_bg`)
          var rateHtml = `<span class='${data.changeRate.indexOf("-") > -1 ? 'red' : 'green'}'>${data.changeRate}</span>`
          //  插入
          high.innerHTML = this.$public.transPrice(data.high, data.code)
          low.innerHTML = this.$public.transPrice(data.low, data.code)
          // 节点为数组的循环插入
          this.roundIn(cnyPrice, this.$public.toIntercept(data.cnyPrice, 2))
          this.roundIn(changeRate, rateHtml)
          this.roundIn(price, this.$public.transPrice(data.price, data.code))
          this.roundIn(volume, this.$public.toIntercept(data.volume, 2))
          if (bg) {
            if (data.changeRate.indexOf('-') > -1) {
              bg.classList.remove("bg-green")
              bg.classList.add("bg-red")
            } else {
              bg.classList.remove("bg-red")
              bg.classList.add("bg-green")
            }
          }
        }
        this.mainPusher.onerror = (msg) => {
          console.log("  websocket.onerror in mainPusher");
        };
      },
      roundIn(nodeList, val) {
        for (var i = 0; i < nodeList.length; i++) {
          nodeList[i].innerHTML = val
        }
      },
      // ajax获取
      getQuotationList() {
        indexGetpro().then((r) => {
          if (r.data.status == 200) {
            this.data = r.data.data
            // 开启推送
          } else {
            this.$public.msg(r.data.msg, 'error', this)
          }

        }).catch((r) => { console.log(r); })
      },
      // 获取轮播
      getBanner() {
        var type = this.$cookies.get("language")=='Chinese' ? 2 : 3
        bannerfind({ type }).then((r) => {
          if (r.data.status == 200) {
            this.bannerData = r.data.data
            this.$nextTick(() => {
              this.swiper = new Swiper('.home_slider', {
                loop: true, // 循环模式选项
                autoplay: {
                  delay: 2000
                },
              })
            })
          }
        })
      },
      // 获取公告
      getNotice() {
        zixun({ size: 3 }).then((r) => {
          if (r.data.status == 200) {
            this.noticeData = r.data.data.res
          }
        })
      }

    },
    created() {
      this.getQuotationList()
      this.$nextTick(this.mainPush)
      this.getBanner()
      this.getNotice()
      this.theme = this.$cookies.get("theme")? this.$cookies.get("theme") :'day'
      this.$bus.$on("changeTheme", (val)=>{
          this.theme = val
      })
    },
    beforeDestroy() {
      this.mainPusher.close()
    }

  }

</script>


